.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.full-page-element {
  width: 80%;
  height: 50%;
  display: flex;
  padding: 20px;
  margin: auto;
  cursor: pointer;
  margin-top: 10rem;
  position: relative;
  border: 1px solid;
  border-radius: 10%;
}
.full-page-sapn {
  position: absolute;
  top: 3%;
  right: 8%;
  font-size: 30px;
}
.full-page-img {
  width: 100%;
  height: 50%;
  margin: auto;
}
