.card-item {
  position: relative;
  width: 200px;
  border-radius: 10%;
  margin: 20px 5px;
  padding: 10px;
  cursor: pointer;
  border: 1px solid #000;
}
.card-item img {
  max-width: 100%;
  height: 100%;
  border-radius: 10%;
  text-align: center;
}
.item-id-top {
  position: absolute;
  font-size: 25px;
  top: 1%;
  right: 0%;
  width: 20px;
  height: 20px;
  line-height: -5;
}
